import {Button, CodeBlock, Modal, ModalVariant} from "@patternfly/react-core";
import React, {useEffect, useState} from "react";
import { getSSOBaseUrl } from "../../../Utils/general-utils";
import {useChrome} from "@redhat-cloud-services/frontend-components/useChrome";
import {useTestingContext} from "./idp-testing-provider";

interface Props {
    idpAlias: string
}

const ValidationModal = ({ idpAlias }: Props) => {
    const chrome = useChrome();
    const ssoBaseUrl = getSSOBaseUrl(chrome);

    const { isModalVisible, toggleModalVisibility } = useTestingContext();

    const [testBtnEnabled, setTestBtnEnabled] = useState(false);
    const [idpTestingModalIcon, setIdpTestingModalIcon] = useState<"info"|"danger"|"success">("info");
    const [idpTestingModalTitle, setIdpTestingModalTitle] = useState("");
    const [idpTestingModalBody, setIdpTestingModalBody] = useState("");
    const [idpTestingModalErrorBody, setIdpTestingModalErrorBody] = useState("");

    const resetTestingModal = () => {
        setTestBtnEnabled(true);
        setIdpTestingModalIcon("info");
        setIdpTestingModalTitle("Identity Provider Testing");
        setIdpTestingModalBody("Select the prompt below to begin testing. This will open a pop-up window that will authenticate with your configured identity provider. If you are not currently signed into this identity provider, please do so in the pop-up window. Please be sure that pop-ups are allowed.");
        setIdpTestingModalErrorBody("");
    }

    useEffect(() => {
        resetTestingModal();
    }, []);

    const dismissTestingModal = () => {
        toggleModalVisibility();
        resetTestingModal();
    }

    const getTestUrl = () => {
        const idpAliasJson = { 'idp_alias': idpAlias }
        const idpAliasJsonString = encodeURIComponent(JSON.stringify(idpAliasJson));
        return `${ssoBaseUrl}protocol/openid-connect/auth?client_id=idp-connectivity-testing-login-client&redirect_uri=${ssoBaseUrl}&response_mode=query&response_type=code&scope=openid&state=${idpAliasJsonString}`;
    }

    const handleTestBtn = () => {
        setTestBtnEnabled(false);
        setIdpTestingModalBody("Please complete identity provider testing in pop-up window");

        window.open(getTestUrl(), "RH IdP Integration Testing", 'width=600,height=600,resizable,scrollbars');

        window.addEventListener("message", (event) => {
            if (ssoBaseUrl.includes(event.origin)) {
                if (event.data.success === true) {
                    setIdpTestingModalIcon("success")
                    setIdpTestingModalTitle("Test successful. Enable for your organization?")
                    setIdpTestingModalBody("Enabling will require users on your organization to authenticate through your newly-configured identity provider on their next authentication. If you do not enable this now, you'll have to re-test again before being allowed to enable this identity provider.")
                } else {
                    setIdpTestingModalIcon("danger")
                    setIdpTestingModalTitle("Test failed")
                    setIdpTestingModalBody(`Try looking over some common mistakes and then re-testing. If that doesnt help, contact support. See additional information below:`)
                    setIdpTestingModalErrorBody(event.data.errorMsg);
                }
            }
        });
    }

    return (
        <Modal
            titleIconVariant={idpTestingModalIcon}
            title={idpTestingModalTitle}
            isOpen={isModalVisible}
            variant={ModalVariant.medium}
            onClose={dismissTestingModal}
            actions={[
                <Button isDisabled={!testBtnEnabled} key="Begin test" variant="primary" onClick={handleTestBtn}>
                    Begin Test
                </Button>,
                <Button key="dismiss" variant="link" onClick={dismissTestingModal}>
                    Cancel
                </Button>
            ]}
        >
            {idpTestingModalBody}
            <CodeBlock hidden={!idpTestingModalErrorBody}>{idpTestingModalErrorBody}</CodeBlock>
        </Modal>
    )
}

export { ValidationModal };
