import React, { createContext, useState, useContext, ReactNode } from 'react';

interface IdPTestingContextType {
    isModalVisible: boolean;
    toggleModalVisibility: () => void;
}

const IdPTestingContext = createContext<IdPTestingContextType | undefined>(undefined);

const useTestingContext = () => {
    const context = useContext(IdPTestingContext);
    if (!context) {
        throw new Error('useTestingContext must be used within a IdPTestingContext');
    }
    return context;
};

const IdpTestingProvider = ({ children }: { children: ReactNode }) => {
    const [isModalVisible, setIsModalVisible] = useState(false);

    const toggleModalVisibility = () => {
        setIsModalVisible((prev) => !prev);
    };

    return (
        <IdPTestingContext.Provider value={{ isModalVisible, toggleModalVisibility }}>
            {children}
        </IdPTestingContext.Provider>
    );
}

export { IdpTestingProvider, useTestingContext };
